.user {
  border: solid 1px $color-border-user-item;
  padding: 10px;
  margin: 5px 0;
  text-align: center;
  cursor: pointer;
  &__email {
    width: 100%;
    font-size: 1.6rem;
    line-height: 1.6rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: $color-border-user-item;
  }
  a {
    text-decoration: none;
  }
}
.user__buttons {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  margin-top: 10px;
  > * {
    width: 100%;
  }
}
